$blur-color: #d4eeff;
$main-text: white;
$button-text: white;
$background: black; 
$button: #0071ba;
$buttonHL: #b8e2fc;
$buttonLL: #003657;

body {
  background-color: $background;
  background-image: url(../../../../public/img/6BWTransFade.png);
}

.movie-view {
  background-image: linear-gradient(
    to bottom, 
    rgba(19, 19, 19, 1.0),
    rgba(19, 19, 19, 0.8),
    rgba(56, 56, 56, 0.5));
  padding: 10px;
  max-width: 90%;
  // box-shadow:
  //   3px 3px 4px white inset,
  //   5px 5px 7px black;
}

.movie-poster {
  width: 80%;
  height: auto;
  box-shadow: 1px 1px 3px black;
}

.label,
.value {
  font-size: medium;
  color: $main-text;
  text-shadow: 1px 1px 3px black;
}

.logOutButton {
  visibility: hidden;
}

.titleh1 {
  color: $main-text;
  font-size: x-large;
  }


.goBackMain,
.goBackMain:hover,
.goBackMain:active,
.goBond,
.goBond:hover,
.goBond:active,
.goGenre,
.goGenre:hover,
.goGenre:active,
.goDirector,
.goDirector:hover,
.goDirector:active  {
  background-color: $button;
  color: $button-text;
  font-size: smaller;
  width: 160px;
  box-shadow: 
  1px 0 1px $buttonHL inset,
  0px 2px 1px $buttonHL inset,
  2px 0 2px $buttonLL inset,
  0px 2px 2px $buttonLL inset;
}

.goBackMain:hover {
  filter: brightness(75%);
}

.goBackMain:active {
  filter: brightness(50%);
}